import React, {FunctionComponent} from "react"
import './style.css'
import CheckBoxAlternative from "../../../checkbox_alternative";
import Note from "../../../note";


const TextBox: FunctionComponent<{ id: string, required?: boolean, promote?: boolean, type?: string, groupTitle: string, position?: string, style?: any, name: string, title: string, description?: string }> = (props) => {

    const {style, id, title, description} = props;

    return <div style={style} onClick={() => document.getElementById(id)?.click()}
                className={`testCheckboxWrapper`}>
        <CheckBoxAlternative {...props}
                             data-wizard-field={true}
                             data-position={props.position}
                             data-group-title={props.groupTitle}
                             title={props.title}
                             name={props.name}
                             description={props.description}
                             id={props.id}
                             className={"textCheckbox"}/>
        <p className={"textCheckboxDescription"}>{description}</p>
        <div className={"textCheckboxTitle"}>
            <h3>{title}</h3>
            {props.promote && <Note>{props.promote}</Note>}
        </div>
    </div>
}

export default TextBox

