export const getProjectScript = (id) => fatch("/api/public/script/" + (id));

export const saveScript = (idProject, value) => fatch(`/api/public/script/${idProject}`, 'POST', {'value': value});


export function translateAnnotation(annotation, workspace) {
    return fatch("/api/translate", "POST", {"id": annotation.id, workspace: workspace});
}

export function deleteAnnotation(workspace, id) {
    return fatch(`/api/public/workspace/${workspace}/annotation/${id}/delete`, 'DELETE');
}

export function saveAnnotation(annotation, workspace) {
    return fatch(`/api/public/workspace/${workspace}/annotation`, "POST", {"annotation": annotation});
}

export function getFiles(workspace, onLoad) {
    return fatch(`/api/public/workspace/${workspace}`).then((data) => {
        onLoad && onLoad(data);
        return data;
    });
}

export function cache(uri, onData) {

    var isLocal = typeof window !== "undefined" && window.location.href.indexOf("127.0.") !== -1;


    var cacheLoad = fatch(uri.replace("/api", isLocal ? '/api' : "/api/cache"), null, null, true); //Cache wird von Cloudflare gecached
    return cacheLoad.then((data) => {
        onData(data, true);

        if (isLocal) {
            return data;
        }

        return fatch(uri).then(d => { //Nachdem die Gecachede Version geladen wurde, schauen ob es was aktuelles gibt
            onData(d, false);
            return d;
        })
    }).catch(() => {
        return fatch(uri).then(d => { //Wenn Cache fehler => Nicht cache versuchen
            onData(d, false);
            return d;
        })
    });
}

// @ts-ignore
export function fatch(uri, method?: string, body ?: any, disableRedirect?: any) {
    if (typeof window === "undefined") {
        return new Promise(r => r());
    }
    return fetch(uri, {
        method: method || "GET",
        body: body && JSON.stringify(body),
        headers: method !== "GET" && {'Content-Type': 'application/json'}
    }).then(function (response) {

        if (response.status === 401 && !disableRedirect) {
            window.location.href = `/`
        }
        const contentType = response.headers.get("content-type");

        if (response.status === 404) {
            return null;
        }

        if (response.status !== 200) {

            if (contentType.indexOf("application/json") !== -1) {
                return response.json().then(json => {
                    throw new Error(json.error || "Fehler");
                })
            } else {
                throw new Error("Fehler");
            }

        }

        if (contentType && contentType.indexOf("application/json") !== -1) {
            var res = response.json();
            return res;
        } else {

            if (method === "GET") {
                throw new Error("API Call has to return JSON. Request " + uri);
            }

            return response;
        }
    }).catch(e => {
        return Promise.reject(e);
    })
}