import React, {FunctionComponent} from "react"
import "./style.css"

const WizardProgressBar: FunctionComponent<{ steps: string[], currentStep: string }> = ({steps, currentStep}) => {
    return <div className={"progressSteps"}>
        {steps.map((step, idx) => {
            return <div id={`tooltip_${step.toLowerCase()}`}
                        className={`progressStep 
            ${idx < steps.indexOf(currentStep) ? 'done' : ''}
            ${step === currentStep ? 'active' : ''}`}>

            </div>
        })}
    </div>
}

export default WizardProgressBar
