// tslint:disable

import React, {useState, useMemo, useCallback, FunctionComponent} from 'react'
// @ts-ignore
import isUrl from 'is-url'
import {Slate, Editable, withReact, useSlate} from 'slate-react'
import {Transforms, Editor, Range, createEditor, Node} from 'slate'
import {withHistory} from 'slate-history'


const LIST_TYPES = ['numbered-list', 'bulleted-list']
const SPEECHBUBBLE = "data:image/svg+xml;base64,";


const LinkExample: FunctionComponent<{ value: Node[], setValue: (value: Node[]) => void }> = ({value, setValue}) => {

    const renderLeaf = useCallback(props => <Leaf {...props} />, [])
    const editor = useMemo(
        () => withLinks(withHistory(withReact(createEditor()))),
        []
    )

    return (
        <Slate editor={editor} value={value} onChange={value => setValue(value)}>

            <Editable
                className={"editorContent"}
                renderLeaf={renderLeaf}
                renderElement={props => <Element {...props} />}
                placeholder={"Text eingeben"}
            />
            <div className={"toolbar"}>
                <BlockButton handle={() => toggleMark(editor, "bold")}
                             format={"Fett"}
                             id={"bold"}
                             icon="data:image/svg+xml;base64,"/>
                <BlockButton handle={() => toggleBlock(editor, "bulleted-list")}
                             format={"Liste"}
                             id={"bulleted-list"}
                             icon="data:image/svg+xml;base64,"/>
                <BlockButton
                    className={"commentButton"}
                    format={"Kommentieren"}
                    id={"commentButton"}
                    icon={SPEECHBUBBLE}
                    handle={() => insertLink(editor, window.prompt("Bitte Kommentar eingeben"))}/>
            </div>

        </Slate>
    )
}

const withLinks = (editor: any) => {
    const {insertData, insertText, isInline} = editor

    editor.isInline = (element: any) => {
        return element.type === 'link' ? true : isInline(element)
    }

    editor.insertText = (text: any) => {
        if (text && isUrl(text)) {
            wrapLink(editor, text)
        } else {
            insertText(text)
        }
    }

    editor.insertData = (data: any) => {
        const text = data.getData('text/plain')

        if (text && isUrl(text)) {
            wrapLink(editor, text)
        } else {
            insertData(data)
        }
    }

    return editor
}

const insertLink = (editor: any, url: any) => {
    if (editor.selection) {
        wrapLink(editor, url)
    }
}

const isLinkActive = (editor: any) => {
    // @ts-ignore
    const [link] = Editor.nodes(editor, {match: n => n.type === 'link'})
    return !!link
}

const unwrapLink = (editor: any) => {
    Transforms.unwrapNodes(editor, {match: n => n.type === 'link'})
}

const wrapLink = (editor: any, url: any) => {
    if (isLinkActive(editor)) {
        unwrapLink(editor)
    }

    const {selection} = editor
    const isCollapsed = selection && Range.isCollapsed(selection)
    const link = {
        type: 'link',
        url,
        children: isCollapsed ? [{text: ''}] : [],
    }

    if (isCollapsed) {
        Transforms.insertNodes(editor, link)
    } else {
        Transforms.wrapNodes(editor, link, {split: true})
        Transforms.collapse(editor, {edge: 'end'})
    }
}
// @ts-ignore
const Element = ({attributes, children, element}) => {
    switch (element.type) {
        case 'link':
            return (
                <a  {...attributes} contentEditable="false" onClick={e => {
                    e.preventDefault();
                    e.stopPropagation();
                }} data-annotation={element.url}>
                    {children}
                    <div contentEditable="false" className={"maker"}/>
                </a>
            )
        case 'bold':
            return <strong {...attributes}>{children}</strong>
        case 'bulleted-list':
            return <ul {...attributes}>{children}</ul>
        case 'heading-one':
            return <h1 {...attributes}>{children}</h1>
        case 'heading-two':
            return <h2 {...attributes}>{children}</h2>
        case 'list-item':
            return <li {...attributes}>{children}</li>
        case 'numbered-list':
            return <ol {...attributes}>{children}</ol>
        default:
            return <p {...attributes}>{children}</p>
    }
}

// @ts-ignore
const Leaf = ({attributes, children, leaf}) => {
    if (leaf.bold) {
        children = <strong>{children}</strong>
    }
    return <span {...attributes}>{children}</span>
}

// @ts-ignore
const toggleBlock = (editor: any, format: any) => {
    const isActive = isBlockActive(editor, format)
    const isList = (LIST_TYPES as any).includes(format)

    Transforms.unwrapNodes(editor, {
        // @ts-ignore
        match: n => (LIST_TYPES as any).includes(n.type),
        split: true,
    })

    Transforms.setNodes(editor, {
        type: isActive ? 'paragraph' : isList ? 'list-item' : format,
    })

    if (!isActive && isList) {
        const block = {type: format, children: []}
        Transforms.wrapNodes(editor, block)
    }
}

const toggleMark = (editor: any, format: any) => {
    const isActive = isMarkActive(editor, format)

    if (isActive) {
        Editor.removeMark(editor, format)
    } else {
        Editor.addMark(editor, format, true)
    }
}

const isBlockActive = (editor: any, format: any) => {
    // @ts-ignore
    const [match] = Editor.nodes(editor, {
        match: n => n.type === format,
    })

    return !!match
}

const isMarkActive = (editor: any, format: any) => {
    const marks = Editor.marks(editor)
    return marks ? marks[format] === true : false
}

const BlockButton: FunctionComponent<{ format: string, className?: string, id: string, handle: () => any, icon: string }> =
    ({format, className, id, handle, icon}) => {
        const editor = useSlate()
        return (
            <div
                data-function={format}
                className={`${className} blockButton ${(isMarkActive(editor, id) || isBlockActive(editor, id)) ? 'active' : ''}`}
                onMouseDown={event => {
                    event.preventDefault()
                    handle();
                }}>
                <img src={icon}/>
            </div>
        )
    }

export default LinkExample
