import React, {FunctionComponent} from "react";

export const WizardRow: FunctionComponent<{ title: string, description: string, value: string }> = ({
                                                                                                                title,
                                                                                                                description,
                                                                                                                value
                                                                                                            }) => {
    return <div className={"wizardDoneContentRow"}>
        <div className={`wizardDoneWrap`}>
            <h3 className={"wizardDoneHeader"}>{title}</h3>
            <p className={"wizardDoneDescription"}>{description}</p>
        </div>
        <p className={"wizardDonePrice"}>{value}</p>
    </div>
}

export const WizardRows: FunctionComponent<{ rows: boolean }> = ({children, rows}) => {
    return <div className={`wizardDoneContent ${rows ? 'rows' : ''}`}>
        {children}
    </div>
}