
import debounce from 'debounce-promise';
import './style.css';
import React, {FunctionComponent, useEffect, useState} from "react"
import RichTextExample from "./editor";
import {Node} from "slate";
import WizardStep from "../index";
import WizardToast from "../toast";
import WizardBody from "../body";

import PQueue from 'p-queue';
import {getProjectScript, saveScript} from "./project.api";

export var EDITED = false;

const queue = new PQueue({concurrency: 1});

const RichtTextWizardStep: FunctionComponent<{ idScript: string }> = (props) => {

    const {idScript} = props;

    function expensiveOperation(value: any) {
        EDITED = true
        setLoading(true);
        return queue.add(() => saveScript(idScript, value).then((data) => {
            setLoading(false);
            return data;
        }).catch((e) => {
            setLoading(false);
            throw e;
        }));
    }


    const [saveCycles] = useState(() => debounce(expensiveOperation, 2000))


    const [value, setValue] = useState([{type: "paragraph", children: [{text: ""}]}])
    const [loading, setLoading] = React.useState(false);

    useEffect(() => (getProjectScript(idScript) as any).then((data: any) => {
        try {
            setValue(JSON.parse(data));
        } catch (e) {
        }
    }), [])

    // @ts-ignore
    return <WizardStep {...props}
                       footerCenter={<WizardToast visible={loading}>
                           {loading ? 'Skript wird gespeichert...' : 'Skript wurde gespeichert'}
                       </WizardToast>}>
        <WizardBody className={"richtextwizard"}>
            <RichTextExample value={value} setValue={(value: any) => {
                const content = JSON.stringify(value)
                setValue(value);
                saveCycles(content).then((value: any) => {
                    console.log(value)
                })
            }}/>

        </WizardBody>
    </WizardStep>
}

export default RichtTextWizardStep