import React, {FunctionComponent, useState} from "react"
import "./style.css"

const ARROW_RIGHT = "data:image/svg+xml;base64,";

function topFunction() {
    document.getElementsByClassName("wizardWrap")[0].scrollIntoView();
}

type WizardProps = {
    header?: string,
    description?: string,
    id?: string,
    disableControl?: boolean,
    hidePrevious?: boolean,
    hideNext?: boolean,
    currentPrice: number,
    image?: string,
    nextButtonText?: string,
    previousButtonText?: string,
    steps?: string[],
    backward: () => {},
    resolve?: () => {},
    footerCenter?: any,
    position?: number,
    forward: () => {},
    step?: any,
    currentStep?: string,
    style?: any,
    className?: string
}

export const isMobile = () => typeof window !== "undefined" && navigator.userAgent.match(/(iPad)|(iPhone)|(iPod)|(android)|(webOS)/i)

const WizardControl: FunctionComponent<WizardProps> = ({backward, resolve, footerCenter, previousButtonText, nextButtonText, step, header, forward, currentStep, steps, currentPrice}) => {

    const [isLoading, setLoading] = useState(false);

    return <div className={"wizardControl"}>

        <button className={"previousButton"} onClick={() => {
            backward && backward();
            isMobile() && topFunction();
        }}>
            <img alt="arrow right icon" src={ARROW_RIGHT}/> {previousButtonText || "Zurück"}
        </button>
        <div className={"price"}>
            {currentPrice > 0 && <p className={"priceLabel"}>Total"</p>}
            {currentPrice > 0 && <b className={"priceValue"}>{`${currentPrice} €`}</b>}
            {footerCenter}
        </div>
        <button className={"nextButton"}
                onClick={() => {

                    setLoading(true);

                    Promise.resolve(forward()).then(function (value) {
                        setLoading(false);
                        console.log(value);
                        isMobile() && topFunction();
                    }).catch(e => {
                        //setLoading(false);
                        throw e;
                    })


                }}>{isLoading ? 'Einen Moment' : (nextButtonText || "Weiter")}

            <img alt="arrow right icon transformed" src={ARROW_RIGHT}/>
        </button>
    </div>
}

export default WizardControl
