import React, {FunctionComponent} from "react"
import {getActiveElements, getFactor, getTotalPrice} from "../index";
import {WizardRow, WizardRows} from "./row";

const WizardFinishStep: FunctionComponent<{}> = ({children}) => {
    return <div className={"finish"}>
        <WizardRows rows={false}>
            {getActiveElements()
                .filter((element: any) => element.price > 0)
                .flatMap((item: any, idx: number) => {
                    return <WizardRow title={item.title} description={item.description}
                                      value={`${(item.price * getFactor()).toFixed(2)} €`}/>;
                })}
        </WizardRows>
        <div className={"wizardDoneFooter"}>
            <div className={"row"}>
                <p className={"wizardDonePrice discount"}>Rabatt</p>
                <p className={"wizardDonePrice discount"}>- €</p>
            </div>

            <div className={"row"}>
                <p className={"wizardDonePrice"}>Total</p>
                <p className={"wizardDonePrice"}>0 €</p>
            </div>
        </div>
    </div>
}

export default WizardFinishStep
