import React, {FunctionComponent, useState} from "react"
import WizardContainer from "../container";
import WizardProgressBar from "../progress";


const WizardComponent: FunctionComponent<{ step: any, hidePrice: boolean, historyEnabled: boolean }> = ({step, hidePrice, historyEnabled}) => {

    const getAllSteps = (step: any) => {

        if (!step.id) {
            console.error("Every Wizard Step need a unique ID");
            throw new Error("Every Wizard Step need a unique ID");
        }

        var steps = step ? [step] : [];
        steps = step.next ? steps.concat(getAllSteps(step.next())) : steps
        console.log("STEPPS");
        console.log(steps);
        return steps;
    }

    const getStepByIdx = (idx: number) => getAllSteps(step)[idx].id;
    const getStepByTooltip = (tooltip: string) => getAllSteps(step).filter(step => step.tooltip === tooltip)[0]?.id;

    const [currentStep, setCurrentStep] = useState(step.id);

    const getCurrentStep = () => getAllSteps(step).filter(step => step.id === currentStep)[0];

    function goToStep(step: string) {
        if (step !== currentStep) {
            historyEnabled && window.history.replaceState && window.history.replaceState(null, step, `${window.location.pathname}#${step}`);
            setCurrentStep(step);
        }
    }

    var steps = getAllSteps(step);

    return <WizardContainer className={`${hidePrice ? 'hidePrice' : ''} active_wizard_${getCurrentStep()?.text}`}>
        <WizardProgressBar currentStep={getCurrentStep()?.id}
                           steps={steps.map(step => step.id)}/>
        <div className={`wizards`}>
            {steps.map((stepL, idx) => stepL.children && <div className={"stepWrapper"} key={stepL.id}>
                {stepL.children({
                    position: idx,
                    backward: () => goToStep(getStepByIdx(Math.max(idx - 1, 0))),
                    goToLast: () => goToStep(getStepByIdx(steps.length - 1)),
                    goToStep: (step: string) => goToStep(step),
                    forward: () => {
                        goToStep(getStepByIdx(Math.min((idx + 1), (steps.length - 1))));
                    },
                    currentStep: stepL.id, step: currentStep, steps: steps,
                    id: stepL.id
                })}
            </div>)}
        </div>
    </WizardContainer>;
};

export default WizardComponent
