import React, {FunctionComponent, useEffect, useState} from "react"
import "./style.css"
import WizardControl from "./wizardcontrol";
import WizardBody from "./body";

export const getElement = (i: any) => i.split('.').reduce((o:any, i:any) => o[i], {});

export const getActiveElements = () => {
    if (typeof window === "undefined") {
        return [];
    }
    // @ts-ignore
    return ([...document.getElementsByClassName("wizardBody")] as any)
        .flatMap((step: any) => {
            return [...step.querySelectorAll("[data-wizard-field]")];
        })
        .filter((element: any) => {
            if (((element.type === 'radio' || element.type === 'checkbox'))) {
                return element.checked;
            }
            return true;
        })
        .map((element: any) => {
            var position = element.getAttribute("data-position") ? getElement(element.getAttribute("data-position")) : null;

            return {
                name: element.getAttribute("name"),
                boxTitle: element.getAttribute("data-box-title"),
                price: position?.price || null,
                value: element.type === 'radio' || element.type === 'checkbox' ? element.getAttribute("title") : element.value,
                groupTitle: element.getAttribute("groupTitle"),
                title: position?.title || element.getAttribute("title"),
                description: position?.description || element.getAttribute("description"),
                factor: position?.factor || 0.0
            };
        })
}
export const getFactor = () => getActiveElements().map((i: any) => i.factor).reduce((pv:any, cv: any) => pv + cv, 0) || 1;
export const getBasePrice = () => Math.round(getActiveElements().filter((item:any) => item.price)
    .map((item: any) => item.price).reduce((pv:any, cv:any) => pv + cv, 0));
export const getTotalPrice = () => Math.round(getBasePrice() * getFactor());
export const getMinute = () => getActiveElements().filter((elements:any) => elements.name === 'timeGroup')[0];
const backgroundImage = (image: string) => ({'background-image': `linear-gradient(to bottom, rgba(255,255,255,0.30) 0%,rgba(255,255,255,0.30) 100%), url(${image})`});


type WizardProps = {
    header: string,
    description: string,
    id: string,
    disableControl: boolean,
    hidePrevious: boolean,
    hideNext: boolean,
    image: string,
    nextButtonText: string,
    previousButtonText: string,
    steps: string[],
    backward: () => {},
    resolve: () => {},
    footerCenter: any,
    position: number,
    forward: () => {},
    step: any,
    currentStep: string,
    style: any,
    className: string
}

const WizardStep: FunctionComponent<WizardProps> = ({
                                                        header, description, id,
                                                        disableControl,
                                                        hidePrevious,
                                                        hideNext,
                                                        image,
                                                        backward,
                                                        nextButtonText,
                                                        previousButtonText,
                                                        steps,
                                                        resolve,
                                                        footerCenter,
                                                        position,
                                                        forward, step, currentStep, style, className, children
                                                    }) => {

    const active = step === currentStep;
    const [totalPrice, setTotalPrice] = useState(getTotalPrice());

    useEffect(() => {
        document.getElementsByTagName("body")[0].addEventListener("checkbox_update",
            () => setTotalPrice(getTotalPrice()))

        setTimeout(() => setTotalPrice(getTotalPrice));
    });

    var wizardHeader = header;
    var wizardDescription = description;

    if (wizardHeader.length >= 28) {
        console.error(`Wizard header should not excedd length of 28 Characters. Current length ${wizardHeader.length}. (${wizardHeader})`);
    }

    if (wizardDescription.length >= 82) {
        console.error(`Wizard description should not excedd length of 82 Characters. Current length ${wizardDescription.length} (${wizardDescription})`);
    }

    return <div id={`wizard_${id.toLowerCase()}`} style={{zIndex: active ? (99999999 - position) : 0}}
                className={`wizard ${active ? 'activeWizard' : ''} ${className} 
                ${hideNext ? 'hideNextButton' : ''}
                ${hidePrevious ? 'hidePreviousButton' : ''}
                ${disableControl ? 'disableWizardControl' : ''}`}>
        <div className={`wizardBody`} style={{...style, ...backgroundImage(image)}}>
            <WizardBody className={"wizardHeaderX"}>
                <h2 className={"wizardHeader"}>{wizardHeader}</h2>
                <p className={"wizardDescription"}>{wizardDescription}</p>
                <div className={"wizardBorder"}/>
            </WizardBody>
            {children}
        </div>
        <WizardControl steps={steps} nextButtonText={nextButtonText}
                       previousButtonText={previousButtonText}
                       footerCenter={footerCenter}
                       forward={forward} backward={backward}
                       step={step} header={header}
                       resolve={resolve}
                       currentStep={currentStep} currentPrice={totalPrice}/>
    </div>
}

export default WizardStep
